/* COM 642 A16 Sass Customization */
.card {
  margin: 10px;
  padding: 10px;
  width: 400px;
  float: left;
  background-color: #f1f1f1;
  color: black;
  border: 1px solid #d3d3d3;
  text-align: center;
  z-index: 9;
  box-shadow: 0px 0px 4px #888;

  button.btn-danger {
    position: relative;
    top: -40px;
    right: -180px;
  }

  button.btn-info {
    position: relative;
    top: -40px;
    left: -180px;
  }

  label {
    margin-right: 4px;
  }
}

.card-title {
  width: 100%;
  cursor: move;
}

.done {
  text-decoration: line-through;
}

.cardContainer {
  margin-top: 36px;
}

#new-item {
  position: fixed;
  bottom: 24px;
  right: 24px;
}

.nav-side {
  box-shadow: 0px 10px 5px #888;  
}

#page-wrapper {
  height: 100%;
  margin-left: 278px;
  background-color: white;
}

.navbar-toggle {
  display: block;
  position: fixed;
  top: 8px;
  left: 8px;
  color: white;
  margin-top: 12px;
  border: none;
  font-size: x-large;

  &:hover {
    background-color: transparent !important;
  }
}

.navbar-brand h1 {
  margin: 0;
  margin-left: 48px;
  margin-top: 12px;
  color: white !important;
}


